import axios from './axios'
// import qs from 'qs'
// import BASE_URL from'../config/index'
const BASE_URa = 'http://sy.zgmng.com/public/index.php'
let token = localStorage.getItem("access_token")
let  data1 = {
    move_id: localStorage.getItem('id'),
    type: localStorage.getItem("type")
}
console.log(data1)
// 登录
export function login(date) {
    return axios.post(
        BASE_URa + '/client/login/index.html', date
    )
}
// 注册
export function register(data) {
    return axios.post(
        BASE_URa + "/client/login/register.html", data,
    )
}
// 发送短信确认
export function send_message(data) {
    return axios.post(
        BASE_URa + "/client/login/send_msg.html", data,
    )
}


